.IconButton {
  margin: 0;
  padding: 5px;
  bottom: none;
  border-radius: 50%;
  border: none;
  color: #3a383d;
  font: inherit;
  background-color: rgb(130, 152, 166);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-left: 12px;
  transition: background-color 250ms linear;
}

.IconButton:hover,
:focus {
  background-color:  #083045;
  color: rgb(76, 181, 245);
}
